
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
html, body {
  padding: 0;
  margin: 0;
  height: 100%;
}
.content-holder {
  min-height: 100%;
  margin-bottom: -2em;
  background-color: #F2F2F2;
}
.content-holder:after {
  content: "";
  display: block;
}
footer, .content-holder:after {
  height: 2em;
}




